@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{--sms-color: var(--success-color);--voice-color: var(--secondary-color)}body{font-family:'Poppins'}.main{height:100vh;position:relative;overflow:hidden}.contacto-icon.small{font-size:18px}.contacto-icon.default{font-size:20px}.contacto-icon--input-prefix{color:var(--gray-1);font-size:20px;margin-right:4px}

.custom-node{--node-color: var(--gray-1);min-width:200px;min-height:100px;background-color:var(--white);border:1px solid var(--node-color);display:flex;flex-direction:column;border-top-width:4px;border-radius:4px 4px 0 0}.custom-node.sms{--node-color: var(--sms-color)}.custom-node.voice{--node-color: var(--voice-color)}.custom-node.start{--node-color: var(--success-color)}.custom-node .node-content{padding:16px}.custom-node .handle-container{display:flex;justify-content:space-evenly;background-color:var(--background-highlight-color)}.custom-node .handle-container .handle-box{display:flex;justify-content:center;align-items:center;flex-direction:column}.custom-node .handle-container .handle-box .react-flow__handle{transform:none}.custom-node .handle-container-top{background-color:var(--white)}.custom-node .handle-container-top .handle-text{display:none}.custom-node .handle-container-top .handle-box{position:relative;top:-8px}.custom-node .handle-container-top .handle-box .react-flow__handle{width:12px;height:12px;border:0;background-color:var(--node-color)}.custom-node .handle-container-bottom{margin-top:auto}.custom-node .handle-container-bottom .handle-box{position:relative;top:5px;padding:0 16px}.custom-node .handle-container-bottom .handle-box .react-flow__handle{width:8px;height:8px;border-radius:2px;background-color:var(--white);border:1px solid var(--secondary-text-color)}.custom-node .handle-container-bottom .handle-box.connected .react-flow__handle{background-color:var(--secondary-text-color)}.custom-node .node-handle{position:static}

.canvas-wrapper{height:100vh;flex-grow:1}.canvas-background{background-color:var(--background-highlight-color)}

.left-bar{width:240px;padding:16px 24px 0;box-shadow:3px 0px 6px rgba(0,0,0,0.06);-webkit-backdrop-filter:blur(27.1828px);backdrop-filter:blur(27.1828px);max-height:100%;overflow:scroll}.left-bar-item{border:1px solid var(--border-color);padding:8px 4px}.left-bar-section{margin-top:16px}

.right-bar{width:464px;transform:translateX(100%);position:absolute;right:0;background-color:var(--white);padding:24px 16px 16px;height:100%;z-index:100;box-shadow:-3px 0px 6px rgba(0,0,0,0.06);transition:transform ease-out 0.2s;box-sizing:border-box}.right-bar.open{transform:translateX(0);transition:transform ease 0.3s}

.event-callbacks-select{width:100px}.event-callbacks-select+.contacto-input-wrapper{flex-grow:1}.json-editor-heading span{width:173px}

